import React from 'react';
import { Label, LabelFontColor, LabelSize } from 'external/src/typography';
import { InputLabelProps as CommonInputLabelProps } from 'shared/types/input';
import { BaseInputSizeExternal } from 'shared/BaseInput/types';

type InputLabelProps = CommonInputLabelProps<BaseInputSizeExternal> & { color: LabelFontColor; size?: LabelSize };

const InputLabel: React.FC<InputLabelProps> = ({
  size = 'small',
  htmlFor,
  color = 'primary',
  text = ''
}): JSX.Element => (
  <Label
    size={ size }
    htmlFor={ htmlFor }
    color={ color }
  >
    { text }
  </Label>
);

const MemoizedInputLabel = React.memo(InputLabel);

export { MemoizedInputLabel };
